<style scoped>

</style>
<template>
  <div>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="合同 id">
          <Input v-model="formData.id" placeholder="请输入合同id" class="filter-input"></Input>
        </FormItem>
        <FormItem label="合同编号">
          <Input v-model="formData.contract_no" placeholder="请输入合同编号" class="filter-input"></Input>
        </FormItem>
        <FormItem label="合同名称">
          <Input v-model="formData.name" placeholder="请输入合同名称" class="filter-input"></Input>
        </FormItem>
        <FormItem label="状态">
          <Select class="filter-input" v-model="formData.status">
            <Option label="待审核" :value="0"></Option>
            <Option label="待签署" :value="1"></Option>
            <Option label="已签署" :value="2"></Option>
            <Option label="即将失效" :value="3"></Option>
          </Select>
        </FormItem>

      </Form>
    </Row>
    <div class="filter-btn">
      <span class="span">操作 </span>
      <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
      <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
    </div>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data">
    </Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>

    <Modal
      v-model="modal"
      :title="'签署合同 '+selectItem.contract_no"
      @on-ok="signedData"
      @on-cancel="cancel">
      <div>签署日期
        <DatePicker v-model="selectItem.sign_date" type="date" placeholder="Select date"
                    style="width: 200px;margin-left: 20px"></DatePicker>
      </div>
      <Row style="margin-top: 20px">
        <Col :span="4">
          扫描件(图片)
        </Col>
        <Col :span="12">
          <image-select v-model="selectItem.picture"></image-select>
        </Col>
      </Row>
    </Modal>
  </div>
</template>

<script>
import api from './api'
import ImageSelect from '../../components/imgSelect'
import * as axios from 'axios'

export default {
  components: {
    ImageSelect
  },
  data () {
    return {
      formData: {},
      modal: false,
      load: false,
      space: [],
      picture: null,
      listData: {},
      selectItem: {},
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '合同编号',
          align: 'center',
          key: 'contract_no'
        },
        {
          title: '订单号',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              attrs: {
                href: '#' + '/order/' + params.row.order.id
              }
            },
            params.row.order.order_no)
          }
        },
        {
          title: '合同模板',
          align: 'center',
          key: 'contract_no'
        },
        {
          title: '创建人',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.operator.nickname)
          }
        },
        {
          title: '合同开始时间',
          align: 'center',
          key: 'begin_time'
        },
        {
          title: '合同结束时间',
          align: 'center',
          key: 'end_time'
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '待审核' : params.row.status === 1 ? '待签署' : params.row.status === 2 ? '已签署' : '即将失效')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginLeft: '5px'
                },
                on: {
                  click: () => {
                    this.downOrder(params.row.id)
                  }
                }
              }, '下载'),
              h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginLeft: '5px',
                  display: params.row.status === 1 ? 'inline' : 'none'
                },
                on: {
                  click: () => {
                    this.signed(params.row)
                  }
                }
              }, '确认签署')
            ])
          }
        }
      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    downOrder (id) {
      axios.request({
        url: 'http://service.fh.81youxi.com/admin/contract/download?id=' + id,
        method: 'POST',
        headers: { 'access-token': this.$store.state.user.admin_token },
        responseType: 'arraybuffer'
      }).then((response) => {
        if (parseInt(response.data.code) === 200) {
          let blob = new Blob([response.data], { type: '' })
          let objectUrl = URL.createObjectURL(blob)
          window.location.href = objectUrl
        }
      }).catch((error) => {
      })
    },
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      this.load = true
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    signedData () {
      let data = { id: this.selectItem.id }
      data.sign_date = this.$formUtil.dataToStr(this.selectItem.sign_date)
      data = this.$formUtil.objToFormData(data)
      if (this.selectItem.picture == null) {
        this.$Message.error('请选择扫描件')
        return
      } else {
        data.append('scan', this.selectItem.picture)
      }
      api.signing(data, (res) => {
        this.cancel()
        this.$Message.success('已签署')
        setTimeout(() => {
          this.loadData()
        }, 500)
      })
    },
    cancel () {
      this.modal = false
      this.selectItem = null
    },
    signed (item) {
      this.modal = true
      this.selectItem = item
      //   this.$Modal.confirm({
      //     title: '确定签署 合同 ' + item.contract_no + ' 吗?',
      //     okText: '签署',
      //     cancelText: '取消',
      //     loading: true,
      //     onOk: () => {
      //       api.signing({id: item.id}, () => {
      //           this.$Modal.remove()
      //           this.$Message.success('签署成功')
      //           this.loadData()
      //         },
      //         () => {
      //           this.$Modal.remove()
      //         })
      //     }
      //   })
      // }
    }
  }
}
</script>
